.App {
  text-align: center;
  margin: 0;
  padding: 0;
}

.App-logo {
  height: auto;
  pointer-events: none;
  width: 90%;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
  font-family: 'Times New Roman', Times, serif;
}

.App-link {
  color: #61dafb;
}

#list {
  text-align: left;
}

.li-label {
  margin-right: 0.25em;
}
